#cards {
  max-width: 930px;
  margin: 0 auto;
  display: flex;
  overflow-x: auto;
  scroll-snap-type: x mandatory;
  gap: 16px;
  padding: 8px;
  scroll-padding: 16px;

  scrollbar-width: none;
}

#title-container {
  display: flex;
  align-items: center;
  justify-content: center;
}

#next24hrs-title {
  width: 930px;
  font-size: 18px;
  padding-left: 8px;
  padding-top: 8px;
}

div#cards::-webkit-scrollbar {
  display: none; /* Chrome, Safari, Edge */
}

#card {
  flex: 0 0 auto;
  scroll-snap-align: start;
  width: 100px;
  height: 150px;
  background-color: #e1e5f2;
  padding: 8px;
  border-radius: 12px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 8px;
}
